@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/../img/icons-fonts/icomoon.eot?ddbsyc');
  src:  url('../fonts/../img/icons-fonts/icomoon.eot?ddbsyc#iefix') format('embedded-opentype'),
    url('../fonts/../img/icons-fonts/icomoon.ttf?ddbsyc') format('truetype'),
    url('../fonts/../img/icons-fonts/icomoon.woff?ddbsyc') format('woff'),
    url('../fonts/../img/icons-fonts/icomoon.svg?ddbsyc#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-arrow:before {
  content: "\e902";
}
.icon-star:before {
  content: "\e900";
}
.icon-check:before {
  content: "\e901";
}
.icon-logo:before {
  content: "\e903";
  color: #fff;
}
